.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 9vh;
  color: #fff;
  display: flex;
  min-height: 0vh;
  
  overflow: hidden;
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out; /* Transitions for sliding and background color animations */
}

.transparent {
  background-color: transparent; /* Make the background transparent */
}

.black-bg {
  background-color: #000; /* Set the background color to black */
}

.slide-up {
  transform: translateY(-100%); /* Slide the navbar up and off the screen */
}
.visible {
  transform: translateY(0%); /* Slide the navbar down and on the screen */
}
.navbar-content {
  display: flex;
  justify-content: space-between; /* Align logo and button to opposite ends */
  align-items: center;
  padding: 0 10px; /* Adjust padding as needed */
  width: 100%;
}

.logo-container {
  display: flex;
  align-items: center;
  margin: 0;
}

.logo {
  height: 50px;
  width: auto;
  margin:0;
}

.logo-text {
  margin: 0;
  font-size: 2.5rem;
}

.menu {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 1.5rem;
}
@media only screen and (max-width: 500px) and (max-height: 900px) {
    .navbar {
      height: 8vh;
    }
    .logo-container {
      display: flex;
      align-items: center;
      margin: 0;
    }
    
    .logo {
      height: 50px;
      width: auto;
      margin:0;
    }
    
    .logo-text {
      margin: 0;
      font-size: 1.5rem;
    }
    
    .menu {
      background: none;
      border: none;
      color: white;
      cursor: pointer;
      font-size: 1rem;
    }
  }
